@font-face {
  font-family: 'FuturaHeader';
  src: url(/src/fonts/FuturaPT-Heavy.woff);
}
@font-face {
  font-family: 'FuturaRegular';
  src: url(/src/fonts/FuturaPT-Medium.woff);
}

body {
  margin: 0;
  font-family: 'FuturaRegular';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-wrapper, .names1, .card-title{font-family: 'FuturaHeader';}

.preloader-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 70px auto;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card {
  display: flex;
  flex-direction: column;
  border-radius: 30px;
  background-color: transparent;
  overflow: hidden;
  ::before {
      position: fixed;
      content: "";
      box-shadow: 0 0 100px 40px #ffffff08;
      top: -10%;
      left: -100%;
      transform: rotate(-45deg);
      height: 60rem;
      transition: .7s all;
    }
  
    &:hover {
      border: 1px solid #ffffff44;
      box-shadow: 0 7px 50px 10px #000000aa;
      transform: scale(1.015);
      filter: brightness(1.2);
      transition: 1s all;
      ::before {
        filter: brightness(.1);
        top: -100%;
        left: 200%;
      }
    }
}

.btn{
  border-radius: 20px;
}

.card-content {
  flex-grow: 1;
}

img,
iframe {
  max-width: 100%;
}

iframe {
  width: 600px;
  height: 350px;
}

.goback{position: fixed;
right: 4%;
bottom: 3.8%;}

.rcp-image{height: 350px;
width:fit-content;}


.rcp{display: flex;
align-items: start;
flex-direction: row;}

.rcp-text{font-size: 18px;
text-align: justify;}

.recipe{display: flex;
  align-items: stretch;
flex-direction: column;}

.info1{margin-left: 50px;}

.card-action{
  display: flex;
  justify-content: center;
}
.info2{margin: 30px 0;}
.input::placeholder {
  font-family: 'FuturaRegular';
}

.m0{margin: 0;}
.w50{width: 50%;}
.Cont-card{margin-bottom: 2.8rem;}
.f14{font-size: 18px;}